import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';

const styles = theme => ({
  dialogContent: {
    minWidth: 400,
  },
  textField: {
    marginBottom: theme.spacing.unit * 2,
  },
});

const deviceTypes = ['Robot', 'Drone', 'Vehicle'];

class DeviceForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = this.getInitialState(props.device);
  }

  componentDidUpdate(prevProps) {
    if (this.props.device !== prevProps.device) {
      this.setState(this.getInitialState(this.props.device));
    }
  }

  getInitialState = (device) => ({
    id: device ? device.vehicleID : '',
    name: device ? device.name || '' : '',
    type: device ? device.type || 'Robot' : 'Robot',
    location: device ? `${device.lat || ''}, ${device.long || ''}` : '',
  });

  handleChange = (field) => (event) => {
    this.setState({ [field]: event.target.value });
  };

  handleSave = () => {
    console.log('Saving device:', this.state);
    // In a real app, you would dispatch an action to save to an API
    alert(`Saving device: ${this.state.name || this.state.id}`);
    this.props.onClose();
  };

  render() {
    const { classes, open, onClose, device } = this.props;
    const { id, name, type, location } = this.state;
    const isEditing = !!device;

    return (
      <Dialog open={open} onClose={onClose} aria-labelledby="device-form-dialog-title">
        <DialogTitle id="device-form-dialog-title">{isEditing ? 'Edit Device' : 'Add New Device'}</DialogTitle>
        <DialogContent className={classes.dialogContent}>
          <TextField
            label="Device ID"
            value={id}
            onChange={this.handleChange('id')}
            fullWidth
            className={classes.textField}
            disabled={isEditing}
            helperText={isEditing ? "ID cannot be changed." : "Unique identifier for the device."}
          />
          <TextField label="Name" value={name} onChange={this.handleChange('name')} fullWidth className={classes.textField} />
          <TextField select label="Type" value={type} onChange={this.handleChange('type')} fullWidth className={classes.textField}>
            {deviceTypes.map(option => (
              <MenuItem key={option} value={option}>{option}</MenuItem>
            ))}
          </TextField>
          <TextField label="Location (Lat, Lng)" value={location} onChange={this.handleChange('location')} fullWidth className={classes.textField} />
        </DialogContent>
        <DialogActions>
          <Button onClick={onClose} color="secondary">Cancel</Button>
          <Button onClick={this.handleSave} color="primary" variant="contained">Save</Button>
        </DialogActions>
      </Dialog>
    );
  }
}

DeviceForm.propTypes = {
  classes: PropTypes.object.isRequired,
  open: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  device: PropTypes.object, // The device to edit, or null for a new one
};

DeviceForm.defaultProps = {
  device: null,
};

export default withStyles(styles)(DeviceForm);