import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import * as config from './config';

const styles = theme => ({
  joystickContainer: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    color: 'white',
  },
  joystick: {
    width: 100,
    height: 100,
  },
  joystickBase: {
    fill: 'rgba(224, 224, 224, 0.4)',
    stroke: 'rgba(189, 189, 189, 0.6)',
    strokeWidth: 2,
  },
  joystickStick: {
    fill: 'rgba(158, 158, 158, 0.7)',
    cursor: 'pointer',
  },
  gripperContainer: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    width: 120,
  },
  gripper: {
    width: 120,
    height: 24,
    cursor: 'pointer',
  },
  gripperTrack: {
    fill: 'rgba(224, 224, 224, 0.4)',
    stroke: 'rgba(189, 189, 189, 0.6)',
    strokeWidth: 1,
  },
  gripperClaw: {
    fill: 'rgba(158, 158, 158, 0.9)',
  },
});

class ArmJoystickController extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      gripperValue: 0,
    };
    this.joystick = { ref: React.createRef(), isDragging: false, lastPosition: null };
    this.gripper = { ref: React.createRef(), isDragging: false };

    this.handleDragMove = this.handleDragMove.bind(this);
    this.handleDragEnd = this.handleDragEnd.bind(this);
    this.handleGripperDragMove = this.handleGripperDragMove.bind(this);
    this.handleGripperDragEnd = this.handleGripperDragEnd.bind(this);
  }

  componentDidMount() {
    const node = this.joystick.ref.current;
    if (node) {
      node.addEventListener('mousedown', this.handleDragStart);
      node.addEventListener('touchstart', this.handleDragStart, { passive: false });
    }
    const gripperNode = this.gripper.ref.current;
    if (gripperNode) {
      gripperNode.addEventListener('mousedown', this.handleGripperDragStart);
      gripperNode.addEventListener('touchstart', this.handleGripperDragStart, { passive: false });
    }
  }

  componentWillUnmount() {
    window.removeEventListener('mousemove', this.handleDragMove);
    window.removeEventListener('mouseup', this.handleDragEnd);
    window.removeEventListener('touchmove', this.handleDragMove);
    window.removeEventListener('touchend', this.handleDragEnd);
    window.removeEventListener('touchcancel', this.handleDragEnd);
    window.removeEventListener('mousemove', this.handleGripperDragMove);
    window.removeEventListener('mouseup', this.handleGripperDragEnd);
    window.removeEventListener('touchmove', this.handleGripperDragMove);
    window.removeEventListener('touchend', this.handleGripperDragEnd);
    window.removeEventListener('touchcancel', this.handleGripperDragEnd);
  }

  handleDragStart = (event) => {
    this.joystick.isDragging = true;
    if (event.type === 'touchstart') {
      event.preventDefault();
      window.addEventListener('touchmove', this.handleDragMove, { passive: false });
      window.addEventListener('touchend', this.handleDragEnd);
      window.addEventListener('touchcancel', this.handleDragEnd);
    } else {
      window.addEventListener('mousemove', this.handleDragMove);
      window.addEventListener('mouseup', this.handleDragEnd);
    }
  };

  getPointerPositionInSVG = (event, svg) => {
    if (!svg) return { x: 0, y: 0 };
    const clientX = event.touches ? event.touches[0].clientX : event.clientX;
    const clientY = event.touches ? event.touches[0].clientY : event.clientY;
    const rect = svg.getBoundingClientRect();
    if (!svg.viewBox.baseVal) return { x: 0, y: 0 };
    const scaleX = svg.viewBox.baseVal.width / rect.width;
    const scaleY = svg.viewBox.baseVal.height / rect.height;
    const x = (clientX - rect.left) * scaleX;
    const y = (clientY - rect.top) * scaleY;
    return { x, y };
  };

  handleDragMove(event) {
    if (!this.joystick.isDragging) return;
    if (event.type === 'touchmove') event.preventDefault();

    const svg = this.joystick.ref.current;
    if (!svg) return;
    const stick = svg.querySelector('.joystick-stick');
    if (!stick) return;

    const { x, y } = this.getPointerPositionInSVG(event, svg);
    const centerX = 50, centerY = 50, maxDistance = 20;
    const dx = x - centerX, dy = y - centerY;
    let distance = Math.sqrt(dx * dx + dy * dy);
    const angle = Math.atan2(dy, dx);

    if (distance > maxDistance) distance = maxDistance;

    const newX = centerX + distance * Math.cos(angle);
    const newY = centerY + distance * Math.sin(angle);

    stick.setAttribute('cx', newX);
    stick.setAttribute('cy', newY);

    const normalizedX = parseFloat(((newX - centerX) / maxDistance).toFixed(2));
    const normalizedY = parseFloat(((newY - centerY) / maxDistance).toFixed(2));
    const armPosition = `a ${normalizedX} ${normalizedY}`;

    if (armPosition !== this.joystick.lastPosition && armPosition !== 'a 0 0') {
      this.props.publish(config.ARM_MQTT_TOPIC, armPosition);
      this.joystick.lastPosition = armPosition;
    }
  }

  handleDragEnd() {
    if (!this.joystick.isDragging) return;
    this.joystick.isDragging = false;

    window.removeEventListener('mousemove', this.handleDragMove);
    window.removeEventListener('mouseup', this.handleDragEnd);
    window.removeEventListener('touchmove', this.handleDragMove);
    window.removeEventListener('touchend', this.handleDragEnd);
    window.removeEventListener('touchcancel', this.handleDragEnd);
  }

  handleGripperDragStart = (event) => {
    this.gripper.isDragging = true;
    if (event.type === 'touchstart') {
      event.preventDefault();
      window.addEventListener('touchmove', this.handleGripperDragMove, { passive: false });
      window.addEventListener('touchend', this.handleGripperDragEnd);
      window.addEventListener('touchcancel', this.handleGripperDragEnd);
    } else {
      window.addEventListener('mousemove', this.handleGripperDragMove);
      window.addEventListener('mouseup', this.handleGripperDragEnd);
    }
  };

  handleGripperDragMove = (event) => {
    if (!this.gripper.isDragging) return;
    if (event.type === 'touchmove') event.preventDefault();

    const svg = this.gripper.ref.current;
    if (!svg) return;

    const { x } = this.getPointerPositionInSVG(event, svg);
    const minX = 10, maxX = 90; // Corresponds to the edges of the track

    let newX = x;
    if (newX < minX) newX = minX;
    if (newX > maxX) newX = maxX;

    // The value is based on how far from the center the user drags
    const centerX = 50;
    const value = Math.abs(newX - centerX) / (maxX - centerX);

    this.updateGripperValue(value);
  };

  handleGripperDragEnd = () => {
    if (!this.gripper.isDragging) return;
    this.gripper.isDragging = false;

    window.removeEventListener('mousemove', this.handleGripperDragMove);
    window.removeEventListener('mouseup', this.handleGripperDragEnd);
    window.removeEventListener('touchmove', this.handleGripperDragMove);
    window.removeEventListener('touchend', this.handleGripperDragEnd);
    window.removeEventListener('touchcancel', this.handleGripperDragEnd);
  };

  updateGripperValue = (value) => {
    const newValue = parseFloat(value.toFixed(2));
    this.setState({ gripperValue: newValue });
    const gripperCommand = `g ${newValue}`;
    this.props.publish(config.GRIPPER_MQTT_TOPIC, gripperCommand);
  };

  render() {
    const { classes } = this.props;
    const { gripperValue } = this.state;
    const center = 50;
    const maxOffset = 40; // 50 - 10
    const offset = gripperValue * maxOffset;
    const leftClawCx = center - offset;
    const rightClawCx = center + offset;

    return (
      <div className={classes.joystickContainer}>
        <Typography variant="caption" color="inherit">Arm</Typography>
        <svg id="arm-joystick" ref={this.joystick.ref} viewBox="0 0 100 100" className={classes.joystick}>
          <circle className={classes.joystickBase} cx="50" cy="50" r="40" />
          <circle className="joystick-stick" cx="50" cy="50" r="20" fill="rgba(158, 158, 158, 0.7)" cursor="pointer" />
        </svg>
        <div className={classes.gripperContainer}>
          <Typography variant="caption" id="gripper-label" color="inherit">Gripper</Typography>
          <svg ref={this.gripper.ref} viewBox="0 0 100 20" className={classes.gripper}>
            <rect x="10" y="8" width="80" height="4" rx="2" className={classes.gripperTrack} />
            <circle cx={leftClawCx} cy="10" r="6" className={classes.gripperClaw} />
            <circle cx={rightClawCx} cy="10" r="6" className={classes.gripperClaw} />
          </svg>
        </div>
      </div>
    );
  }
}

ArmJoystickController.propTypes = {
  classes: PropTypes.object.isRequired,
  publish: PropTypes.func.isRequired,
};

export default withStyles(styles)(ArmJoystickController);