import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import IconButton from '@material-ui/core/IconButton';
import Switch from '@material-ui/core/Switch';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import AddIcon from '@material-ui/icons/Add';
import DeviceForm from './DeviceForm';

const styles = theme => ({
  dialogContent: {
    minWidth: 500,
  },
  addButton: {
    margin: theme.spacing.unit,
  }
});

class DeviceManager extends React.Component {
  state = {
    formOpen: false,
    deviceToEdit: null,
  };

  // In a real app, these handlers would dispatch Redux actions to an API
  handleToggle = (deviceId) => () => {
    console.log(`Toggle visibility for device ${deviceId}`);
    // This would typically update which devices are being subscribed to or displayed.
  };

  handleEdit = (device) => () => {
    console.log('Editing device:', device);
    // This would open an edit form/dialog.
    this.setState({ formOpen: true, deviceToEdit: device });
  };

  handleDelete = (deviceId) => () => {
    console.log('Deleting device:', deviceId);
    // This would show a confirmation and then dispatch a delete action.
    if (window.confirm(`Are you sure you want to delete device ${deviceId}?`)) {
      alert(`Deleting device: ${deviceId}`);
    }
  };

  handleAdd = () => {
    console.log('Adding new device');
    // This would open a form to create a new device.
    this.setState({ formOpen: true, deviceToEdit: null });
  };

  handleFormClose = () => {
    this.setState({ formOpen: false, deviceToEdit: null });
  };

  render() {
    const { classes, onClose, open, devices } = this.props;
    const { formOpen, deviceToEdit } = this.state;

    // This extracts unique devices from the time-series data.
    // For a full implementation, you should fetch a dedicated list of all registered devices.
    const deviceMap = new Map();
    devices.forEach(device => {
      if (device.vehicleID) { // Only consider items that have a vehicleID
        deviceMap.set(device.vehicleID, device);
      }
    });
    const uniqueDevices = Array.from(deviceMap.values());

    return (
      <React.Fragment>
        <Dialog
          open={open}
          onClose={onClose}
          aria-labelledby="device-manager-dialog-title"
        >
          <DialogTitle id="device-manager-dialog-title">Device Management</DialogTitle>
          <DialogContent className={classes.dialogContent}>
            <List>
              {uniqueDevices.map(device => (
                <ListItem key={device.vehicleID}>
                  <ListItemText
                    primary={`Device ID: ${device.vehicleID}`}
                    secondary={`Last seen: ${new Date(device.timestamp).toLocaleString()}`}
                  />
                  <ListItemSecondaryAction>
                    <Switch
                      onChange={this.handleToggle(device.vehicleID)}
                      defaultChecked
                    />
                    <IconButton aria-label="Edit" onClick={this.handleEdit(device)}>
                      <EditIcon />
                    </IconButton>
                    <IconButton aria-label="Delete" onClick={this.handleDelete(device.vehicleID)}>
                      <DeleteIcon />
                    </IconButton>
                  </ListItemSecondaryAction>
                </ListItem>
              ))}
            </List>
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleAdd} color="primary" variant="contained" className={classes.addButton}>
              <AddIcon />
              Add New Device
            </Button>
            <Button onClick={onClose} color="primary">
              Close
            </Button>
          </DialogActions>
        </Dialog>
        <DeviceForm open={formOpen} onClose={this.handleFormClose} device={deviceToEdit} />
      </React.Fragment>
    );
  }
}

DeviceManager.propTypes = {
  classes: PropTypes.object.isRequired,
  onClose: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
  devices: PropTypes.array.isRequired,
};

export default withStyles(styles)(DeviceManager);