import axios from 'axios';

export const UPDATE_VEHICLES = 'UPDATE_VEHICLES';
export const RT_UPDATE_VEHICLES = 'RT_UPDATE_VEHICLES'; // RealTime
export const REALTIME = 'SET_REALTIME';
export const CLEAR_DEVICES = 'CLEAR_DEVICES';

// This points to a local JSON file for development/testing.
const apiUrl_local = './data.json';

// This appears to be a hardcoded ID for a specific vehicle.
const vehicleId = 'dd7295fa-6c65-484d-b38d-30df3bc31c0c';

/**
 * Fetches vehicle history data from a local JSON file.
 * @param {string} dtFrom - The start date/time for the data range.
 * @param {string} dtTo - The end date/time for the data range.
 */
export const fetchVehicles = (dtFrom, dtTo) => {
  return (dispatch) => {
    return axios.get(apiUrl_local, { params: { since: dtFrom, until: dtTo } })
      .then(response => {
        const devices = response.data && response.data.devices;
        if (devices && devices[vehicleId]) {
          dispatch(updateVehicles(devices[vehicleId]));
        } else {
          console.warn(`Vehicle data not found for ID: ${vehicleId}`);
        }
      })
      .catch(error => {
        console.error('Error fetching vehicle data:', error);
        throw(error);
      });
  };
};
/**
 * Creates a Redux action to update the vehicles in the store.
 * @param {Array} vehicles - The array of vehicle data points.
 */
export const updateVehicles = (vehicles) => {
  return {
    type: UPDATE_VEHICLES,
    vehicles
  };
};

/**
 * Creates a Redux action for real-time vehicle updates.
 * @param {Object} vehicles - The real-time vehicle data.
 */
export const rtUpdateVehicles = (vehicles) => {
  return {
    type: RT_UPDATE_VEHICLES,
    vehicles
  };
};