import { UPDATE_VEHICLES, RT_UPDATE_VEHICLES ,CLEAR_DEVICES } from "../actions/vehicles";
const initialState = {
      vehicles : [] // stores devices and data points
  };

const vehiclesReducer = (state = initialState, action) => { 
  switch (action.type) {
    case UPDATE_VEHICLES:
      return { 
        ...state,
        vehicles : state.vehicles.concat(action.vehicles) ,
      };
    case RT_UPDATE_VEHICLES:
      return {
        ...state,
        vehicles: state.vehicles.concat(action.vehicles) ,
      };
    case CLEAR_DEVICES:
      return {...state, 
        vehicles: []
      };
    default:
      return state 
  }
}

export default vehiclesReducer;