import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import Switch from '@material-ui/core/Switch';
import IconButton from '@material-ui/core/IconButton';
import SettingsIcon from '@material-ui/icons/Settings';

const styles = theme => ({
  managerButton: {
    color: 'white',
  },
  dialogContent: {
    minWidth: 300,
  },
});

const controlConfig = [
  { id: 'armJoystick', name: 'Arm Joystick' },
  { id: 'arm6DOF', name: '6-DOF Arm Sliders' },
  { id: 'sixDoFArm', name: 'SVG Arm Control' },
  { id: 'dpad', name: 'D-pad Controls' },
  { id: 'driveControls', name: 'Drive Controls' },
];

class ControlsManager extends React.Component {
  state = {
    open: false,
  };

  handleClickOpen = () => {
    this.setState({ open: true });
  };

  handleClose = () => {
    this.setState({ open: false });
  };

  handleToggle = controlId => () => {
    const { visibleControls, onVisibilityChange } = this.props;
    onVisibilityChange(controlId, !visibleControls[controlId]);
  };

  render() {
    const { classes, visibleControls } = this.props;
    const { open } = this.state;

    return (
      <div>
        <IconButton className={classes.managerButton} onClick={this.handleClickOpen}>
          <SettingsIcon />
        </IconButton>
        <Dialog open={open} onClose={this.handleClose} aria-labelledby="controls-manager-title">
          <DialogTitle id="controls-manager-title">Manage Controls</DialogTitle>
          <DialogContent className={classes.dialogContent}>
            <List>
              {controlConfig.map(control => (
                <ListItem key={control.id}>
                  <ListItemText primary={control.name} />
                  <ListItemSecondaryAction>
                    <Switch onChange={this.handleToggle(control.id)} checked={!!visibleControls[control.id]} />
                  </ListItemSecondaryAction>
                </ListItem>
              ))}
            </List>
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleClose} color="primary">Close</Button>
          </DialogActions>
        </Dialog>
      </div>
    );
  }
}

ControlsManager.propTypes = {
  classes: PropTypes.object.isRequired,
  visibleControls: PropTypes.object.isRequired,
  onVisibilityChange: PropTypes.func.isRequired,
};

export default withStyles(styles)(ControlsManager);