class VideoDeviceOpp extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      sessionStarted: false,
      isPublishing: false, // Start with video off
      showPublisher: false,
      visibleControls: {
        armJoystick: true,
        arm6DOF: true,
        sixDoFArm: false,
        dpad: true,
        driveControls: false,
        // voiceControl: true,
      },
    };
    this.session = null;
    this.publisher = null;
    this.mqttClient = null;
  }

  componentDidMount() {
    this.initializeMqtt();
  }

  componentWillUnmount() {
    // Disconnect from session and MQTT to prevent memory leaks
    if (this.session) {
      this.session.disconnect();
    }
    if (this.mqttClient) {
      this.mqttClient.end();
    }
  }

  handleError = (error) => {
    if (error) {
      console.error('[Vonage Video Error]:', error);
      alert(`Video Error: ${error.message}`);
    }
  }

  initializeSession = () => {
    // This logic is converted from your app.js
    if (!config.APPLICATION_ID || !config.SESSION_ID || !config.TOKEN) {
      if (config.SAMPLE_SERVER_BASE_URL) {
        // Logic to fetch credentials from server can be added here
        alert('Vonage API credentials are not set in src/config.js. Server fetching not yet implemented in this component.');
      } else {
        alert('Vonage API credentials are not set in src/config.js. Cannot start video session.');
      }
      return;
    }

    if (typeof window.OT === 'undefined') {
      alert('OpenTok.js library is not loaded. Please check public/index.html.');
      return;
    }

    this.session = window.OT.initSession(config.APPLICATION_ID, config.SESSION_ID);

    this.session.on('streamCreated', (event) => {
      this.session.subscribe(event.stream, 'subscriber', {
        insertMode: 'replace',
        width: '100%',
        height: '100%'
      }, this.handleError);
    });

    this.publisher = window.OT.initPublisher('publisher', {
      insertMode: 'replace',
      width: '100%',
      height: '100%',
      resolution: '640x480',
      frameRate: 15,
      publishVideo: this.state.isPublishing,
    }, this.handleError);

    this.session.connect(config.TOKEN, (error) => {
      if (error) {
        this.handleError(error);
      } else {
        this.session.publish(this.publisher, this.handleError);
        this.setState({ sessionStarted: true });
      }
    });

  }

  initializeMqtt = () => {
    // Placeholder for MQTT connection logic from app.js

    // Example:
    if (typeof window.mqtt === 'undefined') {
      alert('MQTT.js library is not loaded.');
      return;
    }
    console.log('Initializing MQTT...');

    // this.mqttClient = window.mqtt.connect('wss://your-mqtt-broker.com');
    // this.mqttClient.on('connect', () => console.log('Connected to MQTT'));
    this.mqttClient = window.mqtt.connect(config.MQTT_HOST, config.MQTT_OPTIONS);
    this.mqttClient.on('connect', () => console.log('Connected to MQTT broker'));
    this.mqttClient.on('error', (err) => console.error('MQTT Connection error: ', err));
  };

  publishMqtt = (topic, message) => {
    if (this.mqttClient && this.mqttClient.connected) {
      console.log(`MQTT OUT [${topic}]: ${message}`);
      this.mqttClient.publish(topic, message, config.MQTT_PUBLISH_OPTIONS);
    } else {
      console.warn(`MQTT not connected. Cannot publish to ${topic}.`);
    }
  };

  handlePublishVideo = (shouldPublish) => {
    if (this.publisher) {
      this.publisher.publishVideo(shouldPublish);
      this.setState({ isPublishing: shouldPublish });
    }
  }

  handleControlVisibilityChange = (controlId, isVisible) => {
    this.setState(prevState => ({
      visibleControls: {
        ...prevState.visibleControls,
        [controlId]: isVisible,
      },
    }));
  };

  togglePublisherWindow = () => {
    this.setState(prevState => ({ showPublisher: !prevState.showPublisher }));
  };

  render() {
    const { classes } = this.props;
    const { sessionStarted, isPublishing, visibleControls } = this.state;


    return (
      <div className={classes.root}>
        <div id="videos" className={classes.videos}>
          <div id="subscriber" className={classes.subscriber}></div>
          {this.state.showPublisher && (
            <div id="publisher-container" className={classes.publisherContainer}>
              <div id="publisher" className={classes.publisher}></div>
            </div>
          )}
          <div className={classes.sidePanel}>
            <IconButton
              className={classes.sidePanelButton}
              onClick={!sessionStarted ? this.initializeSession : () => { }}
              style={{ color: sessionStarted ? '#4caf50' : 'white' }}
            >
              <PowerSettingsNewIcon />
            </IconButton>
            <ControlsManager
              visibleControls={visibleControls}
              onVisibilityChange={this.handleControlVisibilityChange}
            />
          </div>
          <div className={classes.controlsContainer}>
            {visibleControls.armJoystick && <ArmJoystickController publish={this.publishMqtt} />}

            {visibleControls.arm6DOF && <Arm6DOFController />}

            {visibleControls.sixDoFArm && <SixDoFArm />}
            {visibleControls.dpad && <DpadController publish={this.publishMqtt} />}

            {visibleControls.driveControls && <DriveControls publish={this.publishMqtt} />}

            {/* {visibleControls.voiceControl && <VoiceControl publish={this.publishMqtt} />} */}
          </div>

        </div>
      </div>
    );
  }
}

VideoDeviceOpp.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(VideoDeviceOpp);