let socket;
class Dashboard extends React.Component {


  constructor(props) {
    super(props);
    this.state = {
      open: false,
      realtime: false,
      rtcolor: "#000",
      dtFrom: new Date().toISOString(),
      dtTo: new Date().toISOString(),
      marker: 0,   // index in devices
      vLength: 0,
      didMount: false,
      tabIndex: 0,
      deviceManagerOpen: false,
      serverSettingsOpen: false,
    };
  }


  componentDidMount() {
    this.setState({ didMount: true });

    socket = io.connect('http://localhost:5000');

    socket.on('connect', function (data) {
      console.log("It worked")
    });

    socket.on('vehicles_update', (data) => {
      console.log("vehicles_update", data)
      let arr = [];
      arr.push(data);
      this.props.actions.updateVehicles(arr);
    });
  }

  componentWillReceiveProps(nextProps) {
    // if(nextProps.value !== this.props.value) {
    //   this.setState({value: nextProps.value});
    // } 
    console.log('compwill-receive props');
    this.setState({ marker: 0 });
  }

  handleDrawerOpen = () => {
    this.setState({ open: true });
  };

  handleDrawerClose = () => {
    this.setState({ open: false });
  };

  handleTabChange = (event, value) => {
    this.setState({ tabIndex: value });
  }


  handleSubmit = (dtFrom, dtTo) => {
    // console.log('from-to' + dtFrom.toTimeString() + '-' + dtTo);
    if (this.state.realtime) {
      this.setState({ realtime: false });
    }

    if (dtFrom && dtTo) {
      console.log('from-to' + dtFrom + '-' + dtTo);
      // alert('dtFrom - dtTo' +  dtFrom + '-' + dtTo);
      dtFrom = this.getISODateString(dtFrom);
      dtTo = this.getISODateString(dtTo);

      console.log('from-to' + dtFrom + '-' + dtTo);
      // this.setState({ realtime: false });
      // var oneWeekAgo = new Date();
      // oneWeekAgo.setDate(oneWeekAgo.getDate() - 7);
      // dtFrom=new Date().getTime().toString();
      // dtTo = "1543788771484";
      if (dtFrom.trim() && dtTo.trim()) {
        this.props.actions.fetchVehicles(dtFrom.trim(), dtTo.trim());
        this.handleReset();
        this.stopPlayMap();
      }
    }
  }

  pad(number) {
    if (number < 10) {
      return '0' + number;
    }
    return number;
  };

  getISODateString(dtnow) {
    dtnow = new Date(dtnow);
    return dtnow.getUTCFullYear() +
      '-' + this.pad(dtnow.getUTCMonth() + 1) +
      '-' + this.pad(dtnow.getUTCDate()) +
      'T' + this.pad(dtnow.getHours()) +
      ':' + this.pad(dtnow.getMinutes()) +
      ':' + this.pad(dtnow.getSeconds());
  }

  handleReset = () => {
    this.setState({
      dtFrom: '',
      dtTo: ''
    });
  };

  handleRealTime = (isRealTime) => {
    console.log('RT Toggled:', isRealTime);
    this.setState({ realtime: isRealTime });
  };

  handleSlideClick = (pos) => {
    console.log(`pos ${pos}`);
    this.stopPlayMap();
    this.setMarker(pos);
  }

  handleMediaControlChange = (step) => {
    console.log('media', step);
    this.stopPlayMap();
    let num = this.state.marker;
    let len = (this.props.vehicles.vehicles || []).length;
    console.log(`mediamarker ${num} , len= ${len - 1}`);
    switch (step) {
      case 0:
        num = 0;
        break;
      case -1:
        num = ((num - 1) > 0) ? num - 1 : 0;
        break;
      case -5:
        this.stopPlayMap();
        return; // Stop playback and do nothing else
      case 5:
        this.playMap(num);
        return; // playMap will handle marker updates
      case 1:
        num = ((num + 1) < len) ? num + 1 : len - 1;
        break;
      case 9:
        num = len - 1;
        break;
      default:
        return; // Do nothing for unknown steps
    }
    console.log(`num  ${num}`);
    this.setMarker(num);
  };

  //  setLoop = setInterval(playMap, 1000);

  playMap(value) {
    if (this.state.didMount && (value >= 0)) {
      const len = (this.props.vehicles.vehicles || []).length;
      if (len === 0) return;

      let currentValue = value;

      const nextFrame = () => {
        this.setMarker(currentValue);
        currentValue = (currentValue + 1) % len;
      };

      this.stopPlayMap(); // Ensure no other loop is running
      nextFrame(); // Show the current frame immediately
      this.setLoop = setInterval(nextFrame, 1000);
    }
  }

  stopPlayMap() {
    if (this.setLoop)
      clearInterval(this.setLoop);
  }

  setMarker(value) {
    console.log('setMarker', value);
    this.setState(() => ({
      marker: value
    }));
  }

  handleDeviceManagerOpen = () => {
    this.setState({ deviceManagerOpen: true });
  };

  handleDeviceManagerClose = () => {
    this.setState({ deviceManagerOpen: false });
  };

  handleServerSettingsOpen = () => {
    this.setState({ serverSettingsOpen: true });
  };

  handleServerSettingsClose = () => {
    this.setState({ serverSettingsOpen: false });
  };


  render() {
    const { classes } = this.props;
    const vehicles = this.props.vehicles.vehicles || [];
    const { realtime, marker, tabIndex } = this.state;
    let ts;
    console.log('marker....', marker);
    if (marker >= 0) {
      if (vehicles.length > 0 && vehicles[marker]) {
        ts = vehicles[marker]["timestamp"];
      } else { ts = marker; }
    } else {
      // console.log('marker null');
    }

    return (
      <div className={classes.pageContainer}>
        <CssBaseline />
        <AppBar submit={this.handleSubmit} realtimeclick={this.handleRealTime} realtime={realtime} onOpenServerSettings={this.handleServerSettingsOpen} />
        <Grid container className={classes.root} spacing={0}>
          {/* <VehicleSocket realtime={realtime}> */}
          <Grid item xs={8} className={classes.demo}>
            <Paper className={classes.mapContainer}>
              <MapCard submit={this.handleSubmit} rtcolor={realtime ? { backgroundColor: "#18ffff" } : { backgroundColor: "red" }} realtime={this.handleRealTime} pos={marker} onOpenDeviceManager={this.handleDeviceManagerOpen} />
            </Paper>
            <Paper className={classes.videoContainer}>
              <VideoDeviceOpp />
            </Paper>
            <div className={classes.controlsContainer}>
              <MediaControlCard max={vehicles.length - 1} value={marker} click={this.handleSlideClick} MediaControlChange={this.handleMediaControlChange} />
            </div>
          </Grid>

          <Grid container className={classes.rside} item xs={4}>
            <Tabs className={classes.tabs1}
              value={tabIndex}
              onChange={this.handleTabChange}
              variant="scrollable"
              scrollButtons="auto"
              centered="false"
              indicatorColor="primary"
              textColor="primary">
              <Tab key='tab1' icon={<InsertChart />} />
              <Tab key='tab2' icon={<VideoCam />} />
              <Tab key='tab3' icon={<Chat />} />
            </Tabs>
            <div style={{ flex: 1, overflowY: 'auto', width: '100%' }}>
              {tabIndex === 0 && <AnalyticsPage pos={ts} handleSubmit={this.handleSubmit} />}
              {tabIndex === 1 && <MediaPage />}
              {tabIndex === 2 && <ChatPage pos={ts} handleSubmit={this.handleSubmit} />}
            </div>
          </Grid>
          {/* </VehicleSocket> */}
          <DeviceManager 
            open={this.state.deviceManagerOpen} 
            onClose={this.handleDeviceManagerClose}
            devices={vehicles}
          />
          <ServerSettings
            open={this.state.serverSettingsOpen}
            onClose={this.handleServerSettingsClose}
          />
        </Grid>
      </div>
    );
  }
}

Dashboard.propTypes = {
  classes: PropTypes.object.isRequired,
};

const mapStateToProps = state => {
  return {
    vehicles: state.vehicles
  }
};
// const mapStateToProps = (state) => state;
const mapDispatchToProps = (dispatch, props) => {
  return {
    actions: bindActionCreators(vehicleActions, dispatch)
  }
}
// const connectDash = connect(mapStateToProps)(Dashboard);
// const withStyleDash = withStyles(styles)(connectDash);

export default withStyles(styles)(connect(mapStateToProps, mapDispatchToProps)(Dashboard));
