import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import * as defaultConfig from './config';

const styles = theme => ({
  dialogContent: {
    minWidth: 500,
  },
  textField: {
    marginBottom: theme.spacing.unit * 2,
  },
  sectionTitle: {
    marginTop: theme.spacing.unit * 2,
    marginBottom: theme.spacing.unit,
  },
});

const CONFIG_STORAGE_KEY = 'mars-geo-tracker-config';

class ServerSettings extends React.Component {
  constructor(props) {
    super(props);
    this.state = this.loadSettings();
  }

  loadSettings = () => {
    try {
      const storedSettings = localStorage.getItem(CONFIG_STORAGE_KEY);
      if (storedSettings) {
        return JSON.parse(storedSettings);
      }
    } catch (error) {
      console.error('Error loading settings from local storage:', error);
    }
    // Fallback to default config if nothing is stored or if there's an error
    return {
      SAMPLE_SERVER_BASE_URL: defaultConfig.SAMPLE_SERVER_BASE_URL,
      APPLICATION_ID: defaultConfig.APPLICATION_ID,
      SESSION_ID: defaultConfig.SESSION_ID,
      TOKEN: defaultConfig.TOKEN,
      MQTT_HOST: defaultConfig.MQTT_HOST,
      MQTT_USERNAME: defaultConfig.MQTT_OPTIONS.username,
      MQTT_PASSWORD: defaultConfig.MQTT_OPTIONS.password,
      MQTT_PATH: defaultConfig.MQTT_OPTIONS.path,
      DRIVE_MQTT_TOPIC: defaultConfig.DRIVE_MQTT_TOPIC,
      ARM_MQTT_TOPIC: defaultConfig.ARM_MQTT_TOPIC,
      GRIPPER_MQTT_TOPIC: defaultConfig.GRIPPER_MQTT_TOPIC,
    };
  };

  handleChange = (field) => (event) => {
    this.setState({ [field]: event.target.value });
  };

  handleSave = () => {
    try {
      localStorage.setItem(CONFIG_STORAGE_KEY, JSON.stringify(this.state));
      alert('Settings saved! Please refresh the page for them to take effect.');
      this.props.onClose();
    } catch (error) {
      console.error('Error saving settings to local storage:', error);
      alert('Failed to save settings.');
    }
  };

  render() {
    const { classes, open, onClose } = this.props;

    return (
      <Dialog open={open} onClose={onClose} aria-labelledby="server-settings-dialog-title">
        <DialogTitle id="server-settings-dialog-title">Server Settings</DialogTitle>
        <DialogContent className={classes.dialogContent}>
          <Typography variant="h6" className={classes.sectionTitle}>Video Server</Typography>
          <TextField
            label="Video Server URL"
            value={this.state.SAMPLE_SERVER_BASE_URL}
            onChange={this.handleChange('SAMPLE_SERVER_BASE_URL')}
            fullWidth
            className={classes.textField}
          />
          <TextField label="Vonage App ID" value={this.state.APPLICATION_ID} onChange={this.handleChange('APPLICATION_ID')} fullWidth className={classes.textField} />
          <TextField label="Vonage Session ID" value={this.state.SESSION_ID} onChange={this.handleChange('SESSION_ID')} fullWidth className={classes.textField} />
          <TextField label="Vonage Token" value={this.state.TOKEN} onChange={this.handleChange('TOKEN')} fullWidth className={classes.textField} multiline />

          <Typography variant="h6" className={classes.sectionTitle}>MQTT Server</Typography>
          <TextField label="MQTT Host" value={this.state.MQTT_HOST} onChange={this.handleChange('MQTT_HOST')} fullWidth className={classes.textField} />
          <TextField label="MQTT Username" value={this.state.MQTT_USERNAME} onChange={this.handleChange('MQTT_USERNAME')} fullWidth className={classes.textField} />
          <TextField label="MQTT Password" value={this.state.MQTT_PASSWORD} onChange={this.handleChange('MQTT_PASSWORD')} fullWidth className={classes.textField} type="password" />
          <TextField label="MQTT Path" value={this.state.MQTT_PATH} onChange={this.handleChange('MQTT_PATH')} fullWidth className={classes.textField} />

          <Typography variant="h6" className={classes.sectionTitle}>MQTT Topics</Typography>
          <TextField label="Drive Topic" value={this.state.DRIVE_MQTT_TOPIC} onChange={this.handleChange('DRIVE_MQTT_TOPIC')} fullWidth className={classes.textField} />
          <TextField label="Arm Topic" value={this.state.ARM_MQTT_TOPIC} onChange={this.handleChange('ARM_MQTT_TOPIC')} fullWidth className={classes.textField} />
          <TextField label="Gripper Topic" value={this.state.GRIPPER_MQTT_TOPIC} onChange={this.handleChange('GRIPPER_MQTT_TOPIC')} fullWidth className={classes.textField} />
        </DialogContent>
        <DialogActions>
          <Button onClick={onClose} color="secondary">Cancel</Button>
          <Button onClick={this.handleSave} color="primary" variant="contained">Save</Button>
        </DialogActions>
      </Dialog>
    );
  }
}

ServerSettings.propTypes = {
  classes: PropTypes.object.isRequired,
  open: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
};

export default withStyles(styles)(ServerSettings);