import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import IconButton from '@material-ui/core/IconButton';
import ArrowUpward from '@material-ui/icons/ArrowUpward';
import ArrowDownward from '@material-ui/icons/ArrowDownward';
import ArrowBack from '@material-ui/icons/ArrowBack';
import ArrowForward from '@material-ui/icons/ArrowForward';
import StopIcon from '@material-ui/icons/Stop';
import Typography from '@material-ui/core/Typography';
import * as config from './config';

const styles = theme => ({
  dpadContainer: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    color: 'white',
  },
  arrowButtonContainer: {
    display: 'grid',
    gridTemplateColumns: 'repeat(3, 1fr)',
    gridTemplateRows: 'repeat(3, 1fr)',
    width: 120,
    height: 120,
    alignItems: 'center',
    justifyItems: 'center',
  },
  arrowButton: {
    color: '#9e9e9e', // Grey color similar to other controls
    backgroundColor: 'rgba(0, 0, 0, 0.1)', // Default transparent background
    // Provide a slightly more opaque background on hover for better feedback
    '&:hover': {
      backgroundColor: 'rgba(0, 0, 0, 0.2)',
    },
  },
  stopButton: {
    color: '#9e9e9e', // Grey color similar to other controls
    backgroundColor: 'rgba(224, 224, 224, 0.4)', // Background like joystick base
    '&:hover': {
      // A slightly darker background on hover for feedback
      backgroundColor: 'rgba(189, 189, 189, 0.6)',
    },
  },
});

class DpadController extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      // isManuallyStopped will be used if you want visual feedback on the stop button
      isManuallyStopped: false,
      lockedArrowDirection: null,
    };
    this.lastArrowClick = { direction: null, time: 0 };
  }

  handleArrowClick = (direction) => {
    if (this.state.lockedArrowDirection === direction) {
      this.setState({ lockedArrowDirection: null, isManuallyStopped: false });
      this.props.publish(config.DRIVE_MQTT_TOPIC, 'stop');
      this.lastArrowClick = { direction: null, time: 0 };
      return;
    }

    const now = Date.now();
    if (this.lastArrowClick.direction === direction && (now - this.lastArrowClick.time) < 400) {
      this.setState({ lockedArrowDirection: direction, isManuallyStopped: false });
      this.lastArrowClick = { direction: null, time: 0 };
      const lockCommandMap = { 'forw': 'fwx', 'back': 'bax', 'left': 'lex', 'right': 'rtx' };
      this.props.publish(config.DRIVE_MQTT_TOPIC, lockCommandMap[direction]);
    } else {
      this.setState({ isManuallyStopped: false });
      this.lastArrowClick = { direction, time: now };
      this.props.publish(config.DRIVE_MQTT_TOPIC, direction);
    }
  };

  handleStopClick = () => {
    this.setState({ isManuallyStopped: true, lockedArrowDirection: null });
    this.lastArrowClick = { direction: null, time: 0 };
    this.props.publish(config.DRIVE_MQTT_TOPIC, 'drstp');
  };

  render() {
    const { classes } = this.props;
    const { lockedArrowDirection } = this.state;

    return (
      <div className={classes.dpadContainer}>
        <Typography variant="caption" color="inherit">Drive (Arrows)</Typography>
        <div className={classes.arrowButtonContainer}>
          <div style={{ gridColumn: 2, gridRow: 1 }}><IconButton color={lockedArrowDirection === 'forw' ? 'secondary' : 'inherit'} className={classes.arrowButton} onClick={() => this.handleArrowClick('forw')}><ArrowUpward /></IconButton></div>
          <div style={{ gridColumn: 2, gridRow: 2 }}><IconButton className={classes.stopButton} onClick={this.handleStopClick}><StopIcon /></IconButton></div>
          <div style={{ gridColumn: 1, gridRow: 2 }}><IconButton color={lockedArrowDirection === 'left' ? 'secondary' : 'inherit'} className={classes.arrowButton} onClick={() => this.handleArrowClick('left')}><ArrowBack /></IconButton></div>
          <div style={{ gridColumn: 3, gridRow: 2 }}><IconButton color={lockedArrowDirection === 'right' ? 'secondary' : 'inherit'} className={classes.arrowButton} onClick={() => this.handleArrowClick('right')}><ArrowForward /></IconButton></div>
          <div style={{ gridColumn: 2, gridRow: 3 }}><IconButton color={lockedArrowDirection === 'back' ? 'secondary' : 'inherit'} className={classes.arrowButton} onClick={() => this.handleArrowClick('back')}><ArrowDownward /></IconButton></div>
        </div>
      </div>
    );
  }
}

DpadController.propTypes = {
  classes: PropTypes.object.isRequired,
  publish: PropTypes.func.isRequired,
};

export default withStyles(styles)(DpadController);