import React from 'react';
import ReplayIcon from '@material-ui/icons/Replay';

// SixDoFArm - React 16.5 compatible (class component, no hooks)
// Simple SVG serial 6-DOF arm. Joints are draggable around their pivots.
// - Rendered with SVG lines (links) and circles (joints/pivots)
// - Click/touch a pivot and drag to rotate that joint; children move with it
// - Keeps everything in component state (angles in radians)

class SixDoFArm extends React.Component {
  static defaultProps = {
    width: 600,
    height: 400,
    base: { x: 100, y: 300 },
    lengths: [15, 60, 50, 40, 35, 30],
    jointRadius: 10,
  };

  constructor(props) {
    super(props);
    // angles for 6 joints (radians)
    this.state = {
      angles: [0, 0.2, -0.3, 0.1, -0.2, 0.15],
      dragging: null, // { type: 'joint', idx, startMouseAngle, startAngle } or { type: 'ee' }
    };

    this.svgRef = React.createRef();
    this._onMouseMove = this._onMouseMove.bind(this);
    this._onMouseUp = this._onMouseUp.bind(this);
    this._onEEMouseMove = this._onEEMouseMove.bind(this);
    this._onEEUp = this._onEEUp.bind(this);
  }

  componentWillUnmount() {
    // Cleanup listeners
    document.removeEventListener('mousemove', this._onMouseMove);
    document.removeEventListener('mouseup', this._onMouseUp);
    document.removeEventListener('touchmove', this._onMouseMove);
    document.removeEventListener('touchend', this._onMouseUp);
    document.removeEventListener('mousemove', this._onEEMouseMove);
    document.removeEventListener('mouseup', this._onEEUp);
    document.removeEventListener('touchmove', this._onEEMouseMove);
    document.removeEventListener('touchend', this._onEEUp);
  }

  // compute world positions of each joint (base included)
  // returns array of points: [{x,y}, ...] length 7 (base + 6 joints)
  computePositions() {
    const { base, lengths } = this.props;
    const { angles } = this.state;
    let points = [{ x: base.x, y: base.y }];
    let ax = base.x;
    let ay = base.y;
    let cum = 0;
    for (let i = 0; i < lengths.length; i++) {
      cum += angles[i];
      const nx = ax + Math.cos(cum) * lengths[i];
      const ny = ay + Math.sin(cum) * lengths[i];
      points.push({ x: nx, y: ny });
      ax = nx;
      ay = ny;
    }
    return points;
  }

  // start dragging a joint
  startDrag(idx, clientX, clientY) {
    const pts = this.computePositions();
    const pivot = pts[idx]; // index: 0=base, 1..6 joints

    // angle between pivot->mouse
    const rect = this.svgRef.current.getBoundingClientRect();
    const mouseX = clientX - rect.left;
    const mouseY = clientY - rect.top;
    const startMouseAngle = Math.atan2(mouseY - pivot.y, mouseX - pivot.x);

    this.setState({
      dragging: {
        type: 'joint',
        idx: idx, // which pivot (0..5) controls joint idx (0..5). base pivot rotates joint 0
        startMouseAngle: startMouseAngle,
        startAngle: this.state.angles[idx] || 0,
        pivotClient: pivot,
      },
    });

    document.addEventListener('mousemove', this._onMouseMove);
    document.addEventListener('mouseup', this._onMouseUp);
    document.addEventListener('touchmove', this._onMouseMove, { passive: false });
    document.addEventListener('touchend', this._onMouseUp);
  }

  _onMouseMove(e) {
    if (!this.state.dragging || this.state.dragging.type !== 'joint') return;
    if (e.preventDefault) e.preventDefault();

    const touch = e.touches && e.touches[0];
    const clientX = touch ? touch.clientX : e.clientX;
    const clientY = touch ? touch.clientY : e.clientY;

    const rect = this.svgRef.current.getBoundingClientRect();
    const mouseX = clientX - rect.left;
    const mouseY = clientY - rect.top;

    const { idx, startMouseAngle, startAngle, pivotClient } = this.state.dragging;
    // recompute pivot in case layout changed
    const pivot = pivotClient;
    const curMouseAngle = Math.atan2(mouseY - pivot.y, mouseX - pivot.x);
    const delta = curMouseAngle - startMouseAngle;

    // update angles[idx] = startAngle + delta
    this.setState(prev => {
      const angles = prev.angles.slice();
      angles[idx] = startAngle + delta;
      return { angles };
    });
  }

  _onMouseUp() {
    if (!this.state.dragging || this.state.dragging.type !== 'joint') return;
    this.setState({ dragging: null });
    document.removeEventListener('mousemove', this._onMouseMove);
    document.removeEventListener('mouseup', this._onMouseUp);
    document.removeEventListener('touchmove', this._onMouseMove);
    document.removeEventListener('touchend', this._onMouseUp);
  }

  // Inverse Kinematics (IK) drag handlers for the end-effector
  handleEEDragStart = (e) => {
    e.preventDefault();
    this.setState({ dragging: { type: 'ee' } });

    document.addEventListener('mousemove', this._onEEMouseMove);
    document.addEventListener('mouseup', this._onEEUp);
    document.addEventListener('touchmove', this._onEEMouseMove, { passive: false });
    document.addEventListener('touchend', this._onEEUp);
  };

  _onEEMouseMove(e) {
    if (!this.state.dragging || this.state.dragging.type !== 'ee') return;
    if (e.preventDefault) e.preventDefault();

    const touch = e.touches && e.touches[0];
    const clientX = touch ? touch.clientX : e.clientX;
    const clientY = touch ? touch.clientY : e.clientY;

    const rect = this.svgRef.current.getBoundingClientRect();
    const targetX = clientX - rect.left;
    const targetY = clientY - rect.top;

    this.solveIK(targetX, targetY);
  }

  _onEEUp() {
    if (!this.state.dragging || this.state.dragging.type !== 'ee') return;
    this.setState({ dragging: null });
    document.removeEventListener('mousemove', this._onEEMouseMove);
    document.removeEventListener('mouseup', this._onEEUp);
    document.removeEventListener('touchmove', this._onEEMouseMove);
    document.removeEventListener('touchend', this._onEEUp);
  }

  // Simple Cyclic Coordinate Descent (CCD) IK solver
  solveIK(targetX, targetY) {
    const numJoints = this.props.lengths.length;
    let angles = this.state.angles.slice();
    const iterations = 5; // More iterations = more accuracy

    for (let iter = 0; iter < iterations; iter++) {
      for (let i = numJoints - 2; i >= 0; i--) {
        const positions = this.computePositions();
        const pivot = positions[i];
        const endEffector = positions[numJoints];

        const vecToEE = { x: endEffector.x - pivot.x, y: endEffector.y - pivot.y };
        const vecToTarget = { x: targetX - pivot.x, y: targetY - pivot.y };

        const angleToEE = Math.atan2(vecToEE.y, vecToEE.x);
        const angleToTarget = Math.atan2(vecToTarget.y, vecToTarget.x);

        let delta = angleToTarget - angleToEE;

        // Clamp the angle change to avoid wild swings
        delta = Math.max(-0.5, Math.min(0.5, delta));

        angles[i] += delta;
        this.setState({ angles });
      }
    }
  }

  // handlers to adapt both mouse and touch
  handlePointerDown = (idx, e) => {
    const touch = e.touches && e.touches[0];
    const clientX = touch ? touch.clientX : e.clientX;
    const clientY = touch ? touch.clientY : e.clientY;
    e.preventDefault();
    this.startDrag(idx, clientX, clientY);
  };

  // reset
  reset = () => {
    this.setState({ angles: [0, 0, 0, 0, 0, 0] });
  };

  render() {
    const { width, height, jointRadius } = this.props;
    const pts = this.computePositions();

    // lines: from pts[i] to pts[i+1]
    const links = [];
    for (let i = 0; i < pts.length - 1; i++) {
      links.push(
        <line
          key={'link-' + i}
          x1={pts[i].x}
          y1={pts[i].y}
          x2={pts[i + 1].x}
          y2={pts[i + 1].y}
          stroke="#222"
          strokeWidth={4}
          strokeLinecap="round"
        />
      );
    }

    // joints: circles at pts[1..6] (joint 0 is at pts[1], but we'll allow rotating joint 0 at pts[0])
    const jointCircles = [];
    // we'll render pivot controls for indices 0..5; each pivot controls angle of joint i
    for (let i = 0; i < 6; i++) {
      const pivotPoint = pts[i]; // pivot for joint i is at pts[i]
      const jointPoint = pts[i + 1];

      // small pivot circle that is draggable (pivot)
      jointCircles.push(
        <g key={'joint-' + i}>
          <circle
            cx={pivotPoint.x}
            cy={pivotPoint.y}
            r={jointRadius}
            fill="#fff"
            stroke="#111"
            strokeWidth={1.5}
            style={{ cursor: 'grab' }}
            onMouseDown={e => this.handlePointerDown(i, e)}
            onTouchStart={e => this.handlePointerDown(i, e)}
          />

          {/* little indicator line showing joint orientation */}
          <line
            x1={pivotPoint.x}
            y1={pivotPoint.y}
            x2={jointPoint.x}
            y2={jointPoint.y}
            stroke="#888"
            strokeWidth={2}
            strokeDasharray="3 3"
          />
        </g>
      );
    }

    // end effector marker
    const ee = pts[pts.length - 1];

    return (
      <svg
        ref={this.svgRef}
        width={width}
        height={height}
        style={{ background: 'transparent' }}
      >
        {/* Reset Icon */}
        <foreignObject
          x={pts[0].x - 12}
          y={pts[0].y + jointRadius + 10}
          width="24"
          height="24"
          onClick={this.reset}
          style={{ cursor: 'pointer' }}
        >
          <ReplayIcon style={{ color: 'white', opacity: 0.7 }} />
        </foreignObject>

        {/* links behind */}
        <g stroke="#fff" stroke-opacity="0.7">{links}</g>
          {/* joint visuals and pivot handles */}
          <g>{jointCircles}</g>

          {/* end effector */}
          <circle
            cx={ee.x} cy={ee.y} r={8} fill="#ff5c5c" stroke="#a33" strokeWidth={1.5}
            style={{ cursor: 'grab' }}
            onMouseDown={this.handleEEDragStart}
            onTouchStart={this.handleEEDragStart}
          />

          {/* base marker */}
        <rect
          x={pts[0].x - (jointRadius + 2)}
          y={pts[0].y - (jointRadius + 2)}
          width={(jointRadius + 2) * 2}
          height={(jointRadius + 2) * 2}
          rx="4"
          fill="#fff"
        />
      </svg>
    );
  }
}

export default SixDoFArm;