import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Slider from '@material-ui/lab/Slider';

const styles = theme => ({
  root: {
    width: 150,
    color: 'white',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  sliderContainer: {
    width: '100%',
    padding: '0 10px',
    boxSizing: 'border-box',
  },
  slider: {
    padding: '10px 0',
  },
  label: {
    fontSize: '0.75rem',
    color: 'inherit',
    textAlign: 'left',
    width: '100%',
  }
});

class Arm6DOFController extends React.Component {
  state = {
    base: 90,
    shoulder: 90,
    elbow: 90,
    wristPitch: 90,
    wristRoll: 90,
    gripper: 0,
  };

  handleChange = (joint) => (event, value) => {
    this.setState({ [joint]: value });
    // In a real implementation, you would send an MQTT message here
    // e.g., this.props.onArmControlChange(joint, value);
    console.log(`Arm Control - ${joint}: ${value}`);
  };

  renderSlider(label, joint, min = 0, max = 240) {
    const { classes } = this.props;
    return (
      <div className={classes.sliderContainer}>
        <Typography className={classes.label} id={`${joint}-label`}>{label}</Typography>
        <Slider
          className={classes.slider}
          value={this.state[joint]}
          min={min}
          max={max}
          onChange={this.handleChange(joint)}
          aria-labelledby={`${joint}-label`}
        />
      </div>
    );
  }

  render() {
    const { classes } = this.props;

    return (
      <div className={classes.root}>
        <Typography variant="caption" color="inherit">6-DOF Arm</Typography>
        {this.renderSlider('Base', 'base')}
        {this.renderSlider('Shoulder', 'shoulder')}
        {this.renderSlider('Elbow', 'elbow')}
        {this.renderSlider('Wrist Pitch', 'wristPitch')}
        {this.renderSlider('Wrist Roll', 'wristRoll')}
        {this.renderSlider('Gripper', 'gripper', 0, 100)}
      </div>
    );
  }
}

Arm6DOFController.propTypes = {
  classes: PropTypes.object.isRequired,
  // onArmControlChange: PropTypes.func.isRequired,
};

export default withStyles(styles)(Arm6DOFController);