import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import * as config from './config';

const styles = theme => ({
  joystickContainer: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    color: 'white',
  },
  joystick: {
    width: 100,
    height: 100,
  },
  joystickBase: {
    fill: 'rgba(224, 224, 224, 0.4)',
    stroke: 'rgba(189, 189, 189, 0.6)',
    strokeWidth: 2,
  },
  joystickAxis: {
    stroke: 'rgba(255, 255, 255, 0.3)',
    strokeWidth: 1,
    strokeDasharray: '2 2',
  },
  joystickStick: {
    fill: 'rgba(158, 158, 158, 0.7)',
    cursor: 'pointer',
  },
  stopButton: {
    backgroundColor: 'red',
    color: 'white',
    marginTop: theme.spacing.unit,
    '&:hover': {
      backgroundColor: '#d32f2f',
    },
  },
});

class DriveControls extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isManuallyStopped: false,
    };
    this.joystick = { ref: React.createRef(), isDragging: false, lastDirection: null };
    this.lastArrowClick = { direction: null, time: 0 };

    this.handleDragMove = this.handleDragMove.bind(this);
    this.handleDragEnd = this.handleDragEnd.bind(this);
  }

  componentDidMount() {
    const node = this.joystick.ref.current;
    if (node) {
      node.addEventListener('mousedown', this.handleDragStart);
      node.addEventListener('touchstart', this.handleDragStart, { passive: false });
      // Add a click listener to the stick itself for the stop action
      const stick = node.querySelector('.joystick-stick');
      if (stick) {
        stick.addEventListener('click', this.handleStickClick);
      }
    }
  }

  componentWillUnmount() {
    window.removeEventListener('mousemove', this.handleDragMove);
    window.removeEventListener('mouseup', this.handleDragEnd);
    window.removeEventListener('touchmove', this.handleDragMove);
    window.removeEventListener('touchend', this.handleDragEnd);
    window.removeEventListener('touchcancel', this.handleDragEnd);
    const node = this.joystick.ref.current;
    if (node) {
      const stick = node.querySelector('.joystick-stick');
      if (stick) {
        stick.removeEventListener('click', this.handleStickClick);
      }
    }
  }

  handleDragStart = (event) => {
    if (event.type === 'touchstart') {
      event.preventDefault();
      window.addEventListener('touchmove', this.handleDragMove, { passive: false });
      window.addEventListener('touchend', this.handleDragEnd);
      window.addEventListener('touchcancel', this.handleDragEnd);
    } else {
      window.addEventListener('mousemove', this.handleDragMove);
      window.addEventListener('mouseup', this.handleDragEnd);
    }
    // Only start dragging if the event target is the stick
    if (event.target.classList.contains('joystick-stick')) {
      this.joystick.isDragging = true;
      this.joystick.hasDragged = false; // Reset drag flag
      this.setState({ isManuallyStopped: false });
    }
  };

  getPointerPositionInSVG = (event, svg) => {
    if (!svg) return { x: 0, y: 0 };
    const clientX = event.touches ? event.touches[0].clientX : event.clientX;
    const clientY = event.touches ? event.touches[0].clientY : event.clientY;
    const rect = svg.getBoundingClientRect();
    if (!svg.viewBox.baseVal) return { x: 0, y: 0 };
    const scaleX = svg.viewBox.baseVal.width / rect.width;
    const scaleY = svg.viewBox.baseVal.height / rect.height;
    return { x: (clientX - rect.left) * scaleX, y: (clientY - rect.top) * scaleY };
  };

  handleDragMove(event) {
    if (!this.joystick.isDragging) return;
    this.joystick.hasDragged = true; // A drag has occurred
    if (event.type === 'touchmove') event.preventDefault();

    const svg = this.joystick.ref.current;
    if (!svg) return;
    const stick = svg.querySelector('.joystick-stick');
    if (!stick) return;

    const { x, y } = this.getPointerPositionInSVG(event, svg);
    const centerX = 50, centerY = 50, maxDistance = 20, snapThreshold = 8;
    const dx = x - centerX, dy = y - centerY;
    let distance = Math.sqrt(dx * dx + dy * dy);
    const angle = Math.atan2(dy, dx);

    if (distance > maxDistance) distance = maxDistance;

    let newX = centerX + distance * Math.cos(angle);
    let newY = centerY + distance * Math.sin(angle);
    let direction = null;

    if (Math.abs(newX - centerX) < snapThreshold && distance > maxDistance / 4) {
      newX = centerX; direction = (newY < centerY) ? 'forw' : 'back';
    } else if (Math.abs(newY - centerY) < snapThreshold && distance > maxDistance / 4) {
      newY = centerY; direction = (newX > centerX) ? 'right' : 'left';
    }

    stick.setAttribute('cx', newX);
    stick.setAttribute('cy', newY);

    if (direction && direction !== this.joystick.lastDirection) {
      this.props.publish(config.DRIVE_MQTT_TOPIC, direction);
      this.joystick.lastDirection = direction;
    } else if (!direction && this.joystick.lastDirection !== null) {
      this.props.publish(config.DRIVE_MQTT_TOPIC, 'stop');
      this.joystick.lastDirection = null;
    }
  }

  handleDragEnd() {
    if (!this.joystick.isDragging) return;

    this.joystick.isDragging = false;

    const svg = this.joystick.ref.current;
    if (svg) {
      const stick = svg.querySelector('.joystick-stick');
      if (stick) { stick.setAttribute('cx', 50); stick.setAttribute('cy', 50); }
    }

    this.joystick.lastDirection = null;

    window.removeEventListener('mousemove', this.handleDragMove);
    window.removeEventListener('mouseup', this.handleDragEnd);
    window.removeEventListener('touchmove', this.handleDragMove);
    window.removeEventListener('touchend', this.handleDragEnd);
    window.removeEventListener('touchcancel', this.handleDragEnd);
  }

  handleArrowClick = (direction) => {
    if (this.state.lockedArrowDirection === direction) {
      this.setState({ lockedArrowDirection: null });
      this.props.publish(config.DRIVE_MQTT_TOPIC, 'stop');
      this.lastArrowClick = { direction: null, time: 0 };
      return;
    }

    const now = Date.now();
    if (this.lastArrowClick.direction === direction && (now - this.lastArrowClick.time) < 400) {
      this.setState({ lockedArrowDirection: direction });
      this.lastArrowClick = { direction: null, time: 0 };
      const lockCommandMap = { 'forw': 'fwx', 'back': 'bax', 'left': 'lex', 'right': 'rtx' };
      this.props.publish(config.DRIVE_MQTT_TOPIC, lockCommandMap[direction]);
    } else {
      this.lastArrowClick = { direction, time: now };
      this.props.publish(config.DRIVE_MQTT_TOPIC, direction);
    }
  }

  handleStop = () => {
    this.setState({ lockedArrowDirection: null });
    this.lastArrowClick = { direction: null, time: 0 };
    this.props.publish(config.DRIVE_MQTT_TOPIC, 'stop');
  }

  handleStickClick = () => {
    // Only treat as a click if no dragging happened
    if (this.joystick.hasDragged) return;

    this.setState({ isManuallyStopped: true });
    this.props.publish(config.DRIVE_MQTT_TOPIC, 'drstp');
  };

  render() {
    const { classes } = this.props;
    const { isManuallyStopped } = this.state;
    const stickColor = isManuallyStopped ? 'rgba(211, 47, 47, 0.35)' : 'rgba(158, 158, 158, 0.7)';

    return (
      <React.Fragment>
        <div className={classes.joystickContainer}>
          <Typography variant="caption" color="inherit">Drive</Typography>
          <svg ref={this.joystick.ref} viewBox="0 0 100 100" className={classes.joystick}>
            <circle className={classes.joystickBase} cx="50" cy="50" r="40" />
            <line x1="50" y1="10" x2="50" y2="90" className={classes.joystickAxis} />
            <line x1="10" y1="50" x2="90" y2="50" className={classes.joystickAxis} />
            <circle className="joystick-stick" cx="50" cy="50" r="20" fill={stickColor} cursor="pointer" />
          </svg>
          <Button variant="contained" className={classes.stopButton} onClick={this.handleStop}>STOP</Button>
        </div>
      </React.Fragment>
    );
  }
}

DriveControls.propTypes = {
  classes: PropTypes.object.isRequired,
  publish: PropTypes.func.isRequired,
};

export default withStyles(styles)(DriveControls);