import React from 'react';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import VideocamOffIcon from '@material-ui/icons/VideocamOff';

import "../node_modules/video-react/dist/video-react.css";
import { Player, BigPlayButton } from 'video-react';

const styles = theme => ({
  root: {
    flexGrow: 1,
    padding: theme.spacing.unit * 2,
    height: '100%',
    overflowY: 'auto',
  },
  videoPaper: {
    padding: theme.spacing.unit,
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
  placeholder: {
    width: '100%',
    paddingTop: '56.25%', // 16:9 Aspect Ratio
    backgroundColor: theme.palette.grey[200],
    position: 'relative',
  },
  placeholderContent: {
    position: 'absolute',
    top: 0,
    left: 0,
    bottom: 0,
    right: 0,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
    color: theme.palette.grey[500],
  },
  icon: {
    fontSize: 48,
    marginBottom: theme.spacing.unit,
  },
});

class MediaPage extends React.Component {
  render() {
    const { classes, vehicles } = this.props;
    const deviceData = vehicles.vehicles || [];

    // This logic currently assumes one device if any data is present.
    // A more robust implementation would depend on having a dedicated list of all registered devices.
    const hasDevices = deviceData.length > 0;
    const devices = hasDevices ? [{ id: 'dd7295fa-6c65-4d-b38d-30df3bc31c0c', src: 'https://www.mapbox.com/bites/00188/patricia_nasa.mp4' }] : [];

    const renderPlaceholders = () => {
      return Array.from(new Array(4)).map((item, index) => (
        <Grid item xs={12} sm={6} key={index}>
          <Paper className={classes.videoPaper}>
            <div className={classes.placeholder}>
              <div className={classes.placeholderContent}>
                <VideocamOffIcon className={classes.icon} />
                <Typography variant="subtitle1">No Device Feed</Typography>
              </div>
            </div>
            <Typography variant="caption" display="block" gutterBottom>
              Placeholder {index + 1}
            </Typography>
          </Paper>
        </Grid>
      ));
    };

    const renderVideoFeeds = () => {
      return devices.map(device => (
        <Grid item xs={12} sm={6} key={device.id}>
          <Paper className={classes.videoPaper}>
            <Player
              playsInline
              poster="/assets/poster.png"
              src={device.src}
            >
              <BigPlayButton position="center" />
            </Player>
            <Typography variant="caption" display="block" gutterBottom>
              Device: {device.id.substring(0, 8)}...
            </Typography>
          </Paper>
        </Grid>
      ));
    };

    return (
      <div className={classes.root}>
        <Grid container spacing={16}>
          {devices.length > 0 ? renderVideoFeeds() : renderPlaceholders()}
        </Grid>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    vehicles: state.vehicles
  }
}

export default withStyles(styles)(connect(mapStateToProps)(MediaPage));
